/*********************
POSTS & CONTENT STYLES
*********************/
.page-title {
	
	// Remove Foundation vcard styling from author page titles
	.vcard {
		border: 0px;
		padding: 0px;
	}
}

html {
	height: 100%;
}

body {
	font-size: 100%;
	height: 100%;
}

.page-template-page-php {
	background: #f5f2eb;
}


h1 {
    text-transform: uppercase;
	font-weight: 600;
	font-family: "din-2014";
	font-size: 28px;
	margin-bottom: 0 !important;
	letter-spacing: 8px;
	@include mq(small) {
	}
	@include mq(medium) {
		font-size: 38px;
	}
}

h2 {
	font-family: "al-fresco";
	font-size: 36px;
	font-weight: 700;
	@include mq(small) {
	}
	@include mq(medium) {
		font-size: 40px;
	}
	@include mq(large) {
		font-size: 46px;
	}
}

h3 {
	text-transform: uppercase;
	font-family: "din-2014";
	font-size: 20px;
	
	@include mq(small) {
	}
	@include mq(medium) {
	}
	@include mq(large) {
	}
}

h4 {
	font-family: "al-fresco";
	text-transform: capitalize;
	font-size: 30px;
	font-weight: 700;
}

p {
	font-family: "din-2014";
	font-weight: normal;
	font-size: 22px;
	margin-top: .5rem;
	a {
		text-decoration: underline !important;
		color: #000 !important;
	}
}

.main {
	a {
		text-decoration: underline !important;
		color: #000 !important;
	}
	ul {
		li {
			strong a {
				text-decoration: underline !important;
				color: #000 !important;
			}
		}
	}
}
.btn-black {
	font-family: "din-2014";
	font-size: 18px;
	background: #000;
	padding: .8rem 2.3rem;
	color: #f0f0da;
	text-decoration: none;
	text-transform: uppercase;
	&:hover {
		background: #680115;
		color: #fff;
	}
}


// Full Background Image
.full-bg {
	background-size: cover !important;
	background-repeat: no-repeat !important;
}

// Post meta  
.byline {
	color: #999;
}

section {
	padding: 2rem 0;
}

.menu .active > a {
    background: transparent !important;
    color: white;
}

// Entry content  
.entry-content {
	
	img {
		max-width: 100%;
		height: auto;
	}

	// .alignleft, img.alignleft { 
	// 	margin-right: 1.5em; 
	// 	display: inline; 
	// 	float: left; 
	// }
	// .alignright, img.alignright { 
	// 	margin-left: 1.5em; 
	// 	display: inline-block; 
	// 	float: right; 
	// }
	.aligncenter, img.aligncenter { 
		margin-right: auto; 
		margin-left: auto; 
		display: block; 
		clear: both; 
	}
			
	video, object {
		max-width: 100%;
		height: auto;
		}

	pre {
		background: #eee;
		border: 1px solid #cecece;
		padding: 10px;
		}

} 

.wp-caption {
	max-width: 100%;
	background: #eee;
	padding: 5px;

	// Images inside wp-caption  
	img {
		max-width: 100%;
		margin-bottom: 0;
		width: 100%;
	}

	p.wp-caption-text {
		font-size: 0.85em;
		margin: 4px 0 7px;
		text-align: center;
	}
} 

// Add Foundation styling to password protect button
.post-password-form input[type="submit"] {
	@include button;
}

/*********************
IMAGE GALLERY STYLES
*********************/
.gallery {
	max-width: 100%;
	margin-left: auto;
	margin-right: auto;
	margin-right: -15px;
	margin-left: -15px; 
}
.gallery::before, .gallery::after {
	content: ' ';
	display: table; 
}
.gallery::after {
	clear: both; 
}
.gallery.gallery-columns-1 > dl {
	@include grid-column;
}
.gallery.gallery-columns-2 > dl {
	@include grid-column(1 of 2);
}
.gallery.gallery-columns-3 > dl {
	@include grid-column(1 of 3);
}
.gallery.gallery-columns-4 > dl {
	@include grid-column(1 of 4);
}
.gallery.gallery-columns-5 > dl {
	@include grid-column(1 of 5);
}
.gallery.gallery-columns-6 > dl {
	@include grid-column(1 of 6);
}
.gallery.gallery-columns-7 > dl {
	@include grid-column(1 of 7);
}
.gallery.gallery-columns-8 > dl {
	@include grid-column(1 of 8);
}
.gallery dl {
	width: 100%;
	float: left;
	padding-left: 0.9375rem;
	padding-right: 0.9375rem; 
}
.gallery dl:last-child:not(:first-child) {
  float: right; 
 }

